// ./services/userService.js
import Cookies from 'js-cookie';
import api from './api'; // 引入共享的 axios 实例

// Base64 解码函数
const base64Decode = (str) => {
  try {
    return JSON.parse(atob(str));
  } catch (e) {
    console.error("Failed to decode base64:", e);
    return null;
  }
};

const UserService = {
  // 注册新用户
  register: async (username, password, inviteCode, accessToken) => {
    const formData = new FormData();
    formData.append("username", username);
    formData.append("password", password);
    formData.append("invite_code", inviteCode);
  
    const response = await api.post('/register/', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        Authorization: `Bearer ${accessToken}`,
      },
    });
  
    return response.data;
  },  

  // 登录
  login: async (username, password, accessToken) => {
    const formData = new FormData();
    formData.append("username", username);
    formData.append("password", password);
  
    const response = await api.post('/login/', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        Authorization: `Bearer ${accessToken}`,
      }
    });
  
    return response.data;
  },
  

  // 刷新 Token
  refreshToken: async (refreshToken) => {
    try {
      const formData = new FormData();
      formData.append("refresh", refreshToken);

      const response = await api.post('/token/refresh/', formData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      });

      if (response.status === 200) {
        return response.data; // 返回用户数据给调用方
      } else {
        console.error("Failed to refresh token. Status:", response.status);
        return null;
      }
    } catch (error) {
      console.error("Error while refreshing token:", error);
      return null;
    }
  },


  // 获取匿名用户的 JWT
  getNonuserJWT: async () => {
    try {
      const response = await api.get('/nonuser/');
      if (response.status === 200) {
        return response.data; // 返回匿名用户数据
      } else {
        console.error("Failed to initialize app. Status:", response.status);
        return null;
      }
    } catch (error) {
      console.error("Error while initializing app:", error);
      return null;
    }
  },

  // 从 Cookies 初始化用户状态
  // 需要在外部接收数据后由调用方决定如何使用返回的 data
  initializeFromCookies: async () => {
    const refresh = Cookies.get('refresh');
    const user_id = Cookies.get('user_id');
    // 检查是否存在 Refresh Token
    if (!refresh) {
      console.log("No refreshToken found in cookies.");
      const userData = await UserService.getNonuserJWT();
      return userData; // 返回匿名用户数据或null
    }

    if (user_id==0){
      const userData = await UserService.getNonuserJWT();
      return userData; // 返回匿名用户数据或null
    }

    // 检查 Refresh Token 格式
    const jwtParts = refresh.split('.');
    if (jwtParts.length !== 3) {
      console.log("Invalid JWT format.");
      const userData = await UserService.getNonuserJWT();
      return userData;
    }

    // 解析 JWT Payload
    const payload = base64Decode(jwtParts[1]);
    if (!payload) {
      console.log("Failed to decode JWT payload.");
      const userData = await UserService.getNonuserJWT();
      return userData;
    }

    // Check token expiration
    const currentTime = Math.floor(Date.now() / 1000);
    if (payload.exp && currentTime >= payload.exp) {
        console.log("Refresh token expired, getting non-user JWT...");
        return await UserService.getNonuserJWT();
    }

    // Try to refresh token
    try {
        const refreshedData = await UserService.refreshToken(refresh);
        if (!refreshedData) {
            console.log("Failed to refresh token, getting non-user JWT...");
            return await UserService.getNonuserJWT();
        }
        return refreshedData;
    } catch (error) {
        console.error("Error during token refresh, getting non-user JWT...", error);
        return await UserService.getNonuserJWT();
    }
  },

  reset_password: async (user_id, newPassword, currentPassword = null, access_token) => {
    try {
      const formData = new FormData();
      formData.append('NewPassword', newPassword);
      if (currentPassword !== null) {
        formData.append('CurrentPassword', currentPassword);
      }
      const response = await api.put(`reset_password/${user_id}`, formData, {
        headers: {
          'Authorization': `Bearer ${access_token}`,
          'Content-Type': 'multipart/form-data',
        }
      });
      if (response.status === 200) {
        return { 
          success: true, 
          message: response.data 
        };
      } else {
        console.error("Failed to reset password. Status:", response.status);
        return { 
          success: false, 
          message: "Failed to reset password" 
        };
      }
    } catch (error) {
      console.error("Error while resetting password:", error);
      // Extract error message from response if available
      const errorMessage = error.response?.data || "An error occurred while resetting password";
      return { 
        success: false, 
        message: errorMessage 
      };
    }
  },

};

export default UserService;
