import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import { Suspense, lazy } from 'react';

// 非懒加载的组件（立即需要的）
import ProtectedRoute from './ProtectedRoute';

// 懒加载组件
// Template Pages
const MainTemplate = lazy(() => import('../pages/template-pages/MainTemplate'));

// Main Pages
const Home = lazy(() => import('../pages/main-pages/Home'));
const Notes = lazy(() => import('../pages/main-pages/Notes'));
const Blogs = lazy(() => import('../pages/main-pages/Blogs'));
const Projects = lazy(() => import('../pages/main-pages/Projects'));
const Finance = lazy(() => import('../pages/main-pages/Finance'));
const Profile = lazy(() => import('../pages/main-pages/Profile'));
const Dashboard = lazy(() => import('../pages/main-pages/Dashboard'));

// Note and Blog Pages
const NotePage = lazy(() => import('../pages/note-pages/NotePage'));
const BlogPage = lazy(() => import('../pages/blog-pages/BlogPage'));
const AddBlogPage = lazy(() => import('../pages/blog-pages/AddBlogPage'));
const EditBlogPage = lazy(() => import('../pages/blog-pages/EditBlogPage'));

// Other Pages
const NotFound = lazy(() => import('../pages/other-pages/NotFound'));
const Signup = lazy(() => import('../pages/legacy-pages/Signup'));
const MarkdownPage = lazy(() => import('../pages/other-pages/MarkdownPage'));

const WeatherWidget = lazy(() => import('../components/dashboard-components/WeatherWidget'));

// Finance Pages
const StockTicker = lazy(() => import('../pages/finance-pages/StockTicker'));

// 加载中组件
const LoadingSpinner = () => (
  <div></div>
);

const AppRoutes = () => {
  return (
    <Router>
      <Suspense fallback={<LoadingSpinner />}>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/blogs" element={<Blogs />} />
          <Route path="/notes" element={<Notes />} />
          <Route path="/notes-cn/:filePath" element={<NotePage />} />
          <Route path="/notes-en/:filePath" element={<NotePage />} />
          <Route path="/md/*" element={<MarkdownPage />} />
          <Route path="/fin" element={<Finance />} />
          {/* <Route path="/signup" element={<Signup />} /> */}
          <Route path="/profile" element={<Profile />} />
          <Route path="/projects" element={<Projects />} />
          <Route path="/blog/:blogID" element={<BlogPage />} />

          <Route path="/dashboard" element={<Dashboard />} />

          {/* Finance Pages */}
          <Route path="/fin/ticker/:ticker" element={<StockTicker />} />

          {/* Template Pages */}
          <Route path="/template" element={<MainTemplate />} />

          {/* <Route path="/test" element={<WeatherWidget />} /> */}

          {/* 受保护的路由：仅 is_staff 为 true 的用户可访问 */}
          <Route 
            path="/addblog" 
            element={
              <ProtectedRoute 
                requiredCondition={(userState) => userState.is_staff}
              >
                <AddBlogPage />
              </ProtectedRoute>
            } 
          />

          <Route 
            path="/editblog/:blogID" 
            element={
              <ProtectedRoute 
                requiredCondition={(userState) => userState.is_staff}
              >
                <EditBlogPage />
              </ProtectedRoute>
            } 
          />
          
          {/* 404 页面 */}
          <Route path="/404" element={<NotFound />} />
          
          {/* 通配符路由 - 捕获所有未定义的路径并重定向到404 */}
          <Route path="*" element={<Navigate to="/404" replace />} />
        </Routes>
      </Suspense>
    </Router>
  );
};

export default AppRoutes;